import { Nav, Navbar } from "rsuite";
import styles from "./MainNavbar.module.css"

const MainNavbar = () => {
  return (
    <>
      <Navbar className={styles.wrapper}>
        <Navbar.Brand href="/"></Navbar.Brand>
        <Nav>
          <Nav.Item href="/login"></Nav.Item>
        </Nav>
      </Navbar>
    </>
  );
};
export default MainNavbar;
